.btn {
  display: inline-block;
  position: relative;
}

.btn::after {
  content: "";
  display: block;
  width: 12.625rem;
  height: 3.375rem;
  border: 1px solid #408F95;
  position: absolute;
  left: 0.1875rem;
  top: 0.25rem;
}

.btn--404::after {
  border: 1px solid #ffffff;
}

.btn--contact:after {
  background-color: #ffffff;
}

.btn__inner {
  display: inline-block;
  width: 12.625rem;
  height: 3.375rem;
  background-color: #408F95;
  border: 1px solid #408F95;
  font-family: "Gotu", sans-serif;
  line-height: 1;
  font-size: 0.875rem;
  position: relative;
  z-index: 1;
  overflow: hidden;
  color: #ffffff;
  transition: 0.6s;
  padding-left: 2.4625rem;
  padding-top: 1.25rem;
  text-align: left;
}

.btn__inner::first-letter {
  text-transform: capitalize;
}

.btn__inner::before {
  display: inline-block;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: transform 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.btn__inner--404::before {
  background-color: #408F95;
}

.btn__inner::after {
  border-bottom: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  content: "";
  height: 0.375rem;
  margin-left: 0.375rem;
  margin-top: 0.125rem;
  position: absolute;
  transform: skew(47deg);
  transition: 0.6s;
  width: 2.5rem;
