[type=text],
[type=search],
[type=tel],
[type=url],
[type=email],
[type=password],
[type=date],
textarea {
  /*↓リセットCSS */
  border: none;
  border-radius: 0;
  background-image: none;
  font-family: inherit;
  /*↓カスタム */
  font-size: 1rem;
  line-height: 1.3;
  padding: 0.5rem 1rem 0.5625rem;
  max-width: 37.5rem;
  width: 100%;
  background: #fff;
  border: 1px solid #408F95;
  border-radius: 0;
}
[type=text]:hover,
[type=search]:hover,
[type=tel]:hover,
[type=url]:hover,
[type=email]:hover,
[type=password]:hover,
[type=date]:hover,
textarea:hover {
  border: 1px solid #408F95;
}
[type=text]:focus,
[type=search]:focus,
[type=tel]:focus,
[type=url]:focus,
[type=email]:focus,
[type=password]:focus,
[type=date]:focus,
textarea:focus {
  border: 1px solid #408F95;
}
@media screen and (min-width: 768px) {
