button,
[type=submit] {
  /*↓リセットCSS */
  margin: 0;
  background-image: none;
  display: inline-block;
  text-decoration: none;
  border-radius: 0;
}

[type=checkbox] {
  display: none;
}
[type=checkbox] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 1.375rem;
  margin: 0 0 0 0;
  font-size: 1rem;
  line-height: 1.4375;
  cursor: pointer;
  transition: 0.3s ease;
}
[type=checkbox] + span::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: #ffffff;
  border: 1px solid #408F95;
  width: 0.875rem;
  height: 0.875rem;
  display: block;
}
[type=checkbox] + span::after {
  opacity: 0;
  transition: 0.3s ease;
}
[type=checkbox]:checked + span::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  display: block;
  opacity: 1;
  /*チェックマークじゃない場合は以下を変更する*/
  transform: translateY(-50%) rotate(-35deg);
  width: 0.7rem;
  height: 0.4375rem;
  margin-top: -0.1em;
  margin-left: 0.1em;
  border-left: 3px solid #C94800;
  border-bottom: 3px solid #C94800;
}

select {
  /*↓リセットCSS */
  appearance: none;
  border-radius: 0;
  font-family: inherit;
  color: inherit;
  border: none;
  cursor: pointer;
  width: 100%;
